<template>
	<div class="manage fillcontain"> 
		<el-row style="height: 100%">
		    <el-col :span="4"  style="min-height: 100%; background-color: #545c64;">
				<el-menu :default-active="defaultActive" style="min-height: 100%;"  class="el-menu-vertical-demo" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b"router>
					<el-menu-item index="manage"><i class="el-icon-menu"></i></el-menu-item>
					<el-submenu index="2">
						<template slot="title"><i class="el-icon-document"></i></template>
						<el-menu-item index="AddArt"></el-menu-item>
						<el-menu-item index="AddSay"></el-menu-item>
						<el-menu-item index="AddLog"></el-menu-item>
					</el-submenu>
					<el-submenu index="3">
						<template slot="title"><i class="el-icon-plus"></i></template>
						<el-menu-item index="ArtList"></el-menu-item>
						<el-menu-item index="SayList"></el-menu-item>
						<el-menu-item index="LogList"></el-menu-item>
					</el-submenu>
					<el-submenu index="4">
						<template slot="title"><i class="el-icon-star-on"></i></template>
						<el-menu-item index="AddCol"></el-menu-item>
						<el-menu-item index="ColList"></el-menu-item>
					</el-submenu>
					<el-submenu index="5">
						<template slot="title"><i class="el-icon-star-on"></i></template>
						<el-menu-item index="AddUser"></el-menu-item>
						<el-menu-item index="UserList"></el-menu-item>
					</el-submenu>
					<el-submenu index="6">
						<template slot="title"><i class="el-icon-setting"></i></template>
						<el-menu-item index="ArtComment"></el-menu-item>
						<el-menu-item index="SayComment"></el-menu-item>
						<el-menu-item index="LogComment"></el-menu-item>
						<el-menu-item index="BoardComment"></el-menu-item>
					</el-submenu>
					<el-submenu index="7">
						<template slot="title"><i class="el-icon-warning"></i></template>
						<el-menu-item index="Explain"></el-menu-item>
						<el-menu-item index="ExplainList"></el-menu-item>
					</el-submenu>
				</el-menu>
			</el-col>
			<el-col :span="20" style="height: 100%;overflow: auto;">
				<router-view></router-view>
			</el-col>
		</el-row>
	</div>
</template>

<script type="text/javascript">
	export default{
		computed: {
			defaultActive: function(){
                return this.$route.path.replace('/', '');
			}
		}
	}
</script>


