<template>
    <div class="AddLog">
      <head-top></head-top>
      <!--  -->
      <el-row  class="art" v-if="show">
          <div class="el-col el-col-12 el-col-offset-4">
                <el-form ref="ruleForm" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="form.desc"></el-input>
                    </el-form-item>
                    <div class="upload">
                        <span></span>
                        <div class="uploadImg">
                            <div v-if="form.image" >
                                <img :src="baseUrl+form.image">
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                            <div v-else>
                                <div class="add">+</div>
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                        </div>
                    </div>
                    <el-form-item label="" prop="newstime">
                        <div class="block">
                            <el-date-picker v-model="form.newstime" type="datetime" placeholder=""></el-date-picker>
                        </div>
                    </el-form-item>
                    <el-form-item label="" prop="open">
                        <el-switch v-model="form.open"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="original">
                        <el-switch v-model="form.original"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="content">
                        <div class="quill-wrap">
                            <quill-editor v-model="form.content" ref="myQuillEditor" :options="editorOption"></quill-editor>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
                        <el-button @click="cancel"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-row>
        <!--  -->
        <el-row  class="art" v-else>
          <div class="el-col el-col-12 el-col-offset-4">
                <el-form ref="ruleForm" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="form.desc"></el-input>
                    </el-form-item>
                    <div class="upload">
                        <span></span>
                        <div class="uploadImg">
                            <div v-if="form.image" >
                                <img :src="baseUrl+form.image">
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                            <div v-else>
                                <div class="add">+</div>
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                        </div>
                    </div>
                    <el-form-item label="" prop="newstime">
                        <div class="block">
                            <el-date-picker v-model="form.newstime" type="datetime" placeholder=""></el-date-picker>
                        </div>
                    </el-form-item>
                    <el-form-item label="" prop="open">
                        <el-switch v-model="form.open"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="original">
                        <el-switch v-model="form.original"></el-switch>
                    </el-form-item>
                    <el-form-item label="" prop="content">
                        <div class="quill-wrap">
                            <quill-editor v-model="form.content" ref="myQuillEditor" :options="editorOption"></quill-editor>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
                        <el-button @click="cancel"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-row>
    </div>
</template>
<script type="text/javascript">
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import headTop from '../public/HeadTop';
    import { quillEditor, Quill } from 'vue-quill-editor';
    import {container, ImageExtend, QuillWatch} from 'quill-image-extend-module';
    Quill.register('modules/ImageExtend', ImageExtend);
    import {logImgUpload, addLog, getOneLog, updateLog} from '../../api/getData';
    import { baseUrl } from '../../config/env';
    export default{
        data(){
            return{
                form:{
                   name:"",
                   desc:"",
                   image:"",
                   newstime:"",
                   open:false,
                   original:false,
                   content: ''
                },
                rules:{
                   name:[
                      {required:true, message:'', trigger:'blur'}
                   ],
                   desc:[
                      {required:true, message:"", trigger:'blur'}
                   ],
                   newstime:[
                      {required:true, message:'', trigger:'blur'}
                   ]
                },
                imageUrl:"",
                show:true,
                id:"",
                baseUrl,
                editorOption: {
                   modules: {
                      ImageExtend: {  // {}  base64 
                         loading: true,
                         name: 'img',  // 
                         action: baseUrl + "?s=Admin/Log/editor",  // , actionbase64
                         // response 
                         // {code: 200; data:{ url: 'baidu.com'}}
                         //  return res.data.url
                         response: (res) => {
                            return baseUrl + res.url;
                         }
                      },
                      toolbar: {  // 
                         container: container,  // container
                         handlers: {
                             'image': function (value) {  // 
                                 QuillWatch.emit(this.quill.id)
                             }
                         }
                      }
                  } 
              }
            }
        },
        components:{headTop,quillEditor},
        created(){
           if(this.$route.query.id){
              this.show = false;
              this.getData(this.$route.query.id);
           }else{
              this.show = true;
           }
        },
        methods:{
           //
           async getData(id){
              var res = JSON.parse(await getOneLog({id}));
              if(res.errcode == 0){
                this.id = res.data.l_id;
                this.form.name = res.data.l_name;
                this.form.desc = res.data.l_desc;
                this.form.image = res.data.l_img;
                this.form.newstime = new Date(res.data.l_time * 1000);
                this.form.open = !!res.data.l_show;
                this.form.original = !!res.data.l_original;
                this.form.content = res.data.l_content;
              }
           },
           //
           onFileChange:function(e){
               var files = e.target.files || e.dataTransfer.files;
               if(!files.length)return
               this.createImage(files[0]);
           },
           createImage: function(file){
               var reader = new FileReader();
               reader.onload = (e) => {
                   this.uploadImg(e.target.result);
                   this.form.image = e.target.result;
               }
               reader.readAsDataURL(file)
           },
           async uploadImg(image){
              var res = JSON.parse(await logImgUpload({image}));
              console.log(res)
              if(res.errcode == 0){
                this.form.image = res.filename;
                this.$message({type:'success', message:res.msg});
              }else{
                this.$message({type:'error', message:res.msg});
              }
           },
           //
           onSubmit(formName) {
              this.$refs[formName].validate(async (valid) => {
                if (valid) {
                     if(sessionStorage.getItem('username')){
                        if(sessionStorage.getItem('class') !== '1'){
                          this.$message({ type:'warning', message:'' });
                          return false;
                        }
                        var data = {};
                        data.l_name = this.form.name;
                        data.l_desc = this.form.desc;
                        data.l_img = this.imageUrl ? this.imageUrl : this.form.image;
                        data.l_time = this.form.newstime.getTime() / 1000;
                        data.l_show = this.form.open ? 1 : 0;
                        data.l_original = this.form.original ? 1 : 0;
                        data.l_root = sessionStorage.getItem('username');
                        data.l_content = this.form.content;
                        if(this.id){
                          data.l_id = this.id;
                          var res = JSON.parse(await updateLog(data));
                        }else{
                          var res = JSON.parse(await addLog(data));
                        } 
                        if(res.errcode == 0){
                           this.$message({type:'success', message:res.msg});
                           this.$router.push('/LogList') 
                        }else{
                           this.$message.error(res.msg);
                        } 
                     }else{
                        this.$router.push("/");
                     }
                }else{
                    this.$notify.error({ title: '', message: '', offset: 100 });
                }
            });
          },
          //
          cancel(){
             this.$router.push('/LogList');
          }
        }
    }
</script>
<style scoped>
.quill-editor{
  height: 465px;
  width: 700px;
  margin-bottom:150px;
} 
.ql-container{
  height: 400px;
}
</style>




