<template>
    <div class="explain">
        <head-top></head-top>
        <!--  -->
        <el-row  class="art" v-if="show">
          <div class="el-col el-col-12 el-col-offset-4">
                <el-form ref="ruleForm" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label="" prop="title">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="keyword">
                        <el-input v-model="form.keyword"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="createtime">
                        <div class="block">
                            <el-date-picker v-model="form.createtime" type="datetime" placeholder=""></el-date-picker>
                        </div>
                    </el-form-item>
                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="form.desc"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="username">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item label="QQ" prop="qq">
                        <el-input v-model="form.qq"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="profession">
                        <el-input v-model="form.profession"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="hobby">
                        <el-input v-model="form.hobby"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="netname">
                        <el-input v-model="form.netname"></el-input>
                    </el-form-item>
                    <div class="upload">
                        <span></span>
                        <div class="uploadImg">
                            <div v-if="form.img" >
                                <img :src="baseUrl+form.img">
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                            <div v-else>
                                <div class="add">+</div>
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                        </div>
                    </div>
                    <el-form-item label="" prop="explain">
                        <el-input type="textarea" v-model="form.explain"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="program">
                        <el-input type="textarea" v-model="form.program"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
                        <el-button @click="cancel"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-row>
        <!--  -->
        <el-row  class="art" v-else>
          <div class="el-col el-col-12 el-col-offset-4">
                <el-form ref="ruleForm" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label="" prop="title">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="keyword">
                        <el-input v-model="form.keyword"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="createtime">
                        <div class="block">
                            <el-date-picker v-model="form.createtime" type="datetime" placeholder=""></el-date-picker>
                        </div>
                    </el-form-item>
                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="form.desc"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="username">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item label="QQ" prop="qq">
                        <el-input v-model="form.qq"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="profession">
                        <el-input v-model="form.profession"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="hobby">
                        <el-input v-model="form.hobby"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="netname">
                        <el-input v-model="form.netname"></el-input>
                    </el-form-item>
                    <div class="upload">
                        <span></span>
                        <div class="uploadImg">
                            <div v-if="form.img" >
                                <img :src="baseUrl+form.img">
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                            <div v-else>
                                <div class="add">+</div>
                                <input type="file" class="file" @change="onFileChange">
                            </div>
                        </div>
                    </div>
                    <el-form-item label="" prop="explain">
                        <el-input type="textarea" v-model="form.explain"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="program">
                        <el-input type="textarea" v-model="form.program"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
                        <el-button @click="cancel"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-row>
    </div>
</template>
<script type="text/javascript">
    import headTop from '../public/HeadTop';
    import {systemImgUpload, addSystem, getOneSystem, updateSystem} from '../../api/getData';
    import { baseUrl } from '../../config/env'; 
    export default{
        data(){
           return {
              show: true,
              form: {
                title: '',
                keyword: ',,study,',
                createtime: '',
                desc: '',
                username: '',
                qq: '1149250421',
                profession: ',php',
                hobby: ',,',
                netname: '',
                img: '',
                explain: "901532, htmlcssjsPHPJava.......~W_W~",
                program: ',,,,,,'
              },
              rules: {
                title: [
                  {required:true, message:'', trigger:'blur'}
                ],
                createtime:[
                  {required:true, message:'', trigger:'blur'}
                ]
              },
              baseUrl,
              id: ''
           }
        },
        components: {headTop},
        created(){
           if(this.$route.query.id){
              this.show = false;
              this.getData(this.$route.query.id);
           }else{
              this.show = true;
           }
        },
        methods: {
           //
           async getData(id){
              var res = JSON.parse(await getOneSystem({id}));
              if(res.errcode == 0){
                 this.id = res.data.id;
                 res.data.createtime = new Date(res.data.createtime * 1000);
                 this.form = res.data;
              }else{
                 this.$message({type:'error', message:res.msg});
              }
           },
           //
           onFileChange:function(e){
               var files = e.target.files || e.dataTransfer.files;
               if(!files.length)return
               this.createImage(files[0]);
           },
           createImage: function(file){
               var reader = new FileReader();
               reader.onload = (e) => {
                   this.uploadImg(e.target.result);
               }
               reader.readAsDataURL(file)
           },
           async uploadImg(image){
              var res = JSON.parse(await systemImgUpload({image}));
              if(res.errcode == 0){
                this.form.img = res.filepath;
                this.$message({type:'success', message:res.msg});
              }else{
                this.$message({type:'error', message:res.msg});
              }
           },
           //
           onSubmit(formName){
                this.$refs[formName].validate(async (valid) =>{
                    if(valid){
                       if(sessionStorage.getItem('username')){
                           if(sessionStorage.getItem('class') !== '1'){
                               this.$message({ type: 'warning', message: '' });
                               return false;
                           }
                           var data = {};
                           data.title = this.form.title;
                           data.keyword = this.form.keyword;
                           data.createtime = this.form.createtime.getTime() / 1000;
                           data.desc = this.form.desc;
                           data.username = this.form.username;
                           data.qq = this.form.qq;
                           data.profession = this.form.profession;
                           data.hobby = this.form.hobby;
                           data.netname = this.form.netname;
                           data.img = this.form.img;
                           data.explain = this.form.explain;
                           data.program = this.form.program;
                           if(this.id){
                             data.id = this.id;
                             var res = JSON.parse(await updateSystem(data));
                           }else{
                             var res = JSON.parse(await addSystem(data));
                           }
                           res.errcode == 0 ? this.$router.push('/ExplainList') : this.$message({ type: 'error', message: res.msg });
                       }else{
                           this.$router.push('/');
                       }
                    }else{
                       this.$notify.error({ title: '', message: '', offset: 100 });
                    }
                })
            },
            cancel(){
                this.$router.push('/ExplainList');
            }
        }
    }
</script>


